* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #e0e8ff;
}

main {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    width: 90vw;
    max-width: 500px;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 20px;
}

h1 {
    background-color: #1b1096;
    color: #fff;
    width: 100%;
    margin-bottom: 10px;
    font-size: 2rem;
}

.output {
    padding: 10px 5px;
    border-radius: 9999px;
    background-color: #1b1096;
    margin-top: 10px;
    color: #fff;

}

.output-section {
    margin-top: 20px;
}

.input-section {
    width: 70%;
}

.input-slider {
    width: 100%;
    margin: 10px;
}